.card-header{
  padding: 16px 16px !important;
}

.card-header h3 {
  font-size: 1.75rem;
  color: #2b8ff4;
  text-align: center;
  padding: 7px 0 7px;
  margin: 20px 25px 0;
  position: relative;
  letter-spacing: 0.2rem;
}

/*
dataTable　対応
*/
.dataTable div {
  font-size: 14px !important;
}

.dataTable .rdt_TableCol {
  text-align: left !important;
  justify-content: flex-start !important;
  font-weight: bold !important;
}

/*
details-table　対応
*/
.table>div.table-body:nth-child(odd) {
  background: white;
}

.table>div.table-body:nth-child(even) {
  background: whitesmoke;
}

.table-body {
  padding:5px;
}

.custom-card {
  background: whitesmoke;
}


.card-body .rdt_TableCol {
  text-align: left !important;
  justify-content: flex-start !important;
  font-weight: bold !important;
}

.card-body .rdt_TableCol >input {
  margin-left: 18px !important;
}

.custom-file-upload-input {
  position: relative;
  z-index: 2;
  width: 100%;
  min-height: 38px;
  margin: 0;
  opacity: 0;
}

.custom-file-upload-input:focus ~ .custom-file-upload-label {
  border-color: #80bdff;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

.custom-file-upload-input[disabled] ~ .custom-file-upload-label,
.custom-file-upload-input:disabled ~ .custom-file-upload-label {
  background-color: #e9ecef;
}

.custom-file-upload-input:lang(en) ~ .custom-file-upload-label::after {
  content: "Browse";
}

.custom-file-upload-input ~ .custom-file-upload-label[data-browse]::after {
  content: attr(data-browse);
}

.custom-file-upload-label {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1;
  min-height: 38px;
  padding: 0.375rem 0.75rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  justify-content: flex-start !important;
}

.custom-file-upload-label::after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 3;
  display: block;
  min-height: 36px;
  padding: 0.375rem 0.75rem;
  line-height: 1.5;
  color: #495057;
  content: "Browse";
  background-color: #e9ecef;
  border-left: inherit;
  border-radius: 0 0.25rem 0.25rem 0;
}

.custom-form-group {
  margin-bottom: 1rem;
}

.react-datepicker-wrapper {
  width: 100%!important;
}

.form-inline label {
  max-width: 100%!important;
  overflow: hidden!important;
}

.w-80 {
  width: 80% !important;
}

@media print {
  body * {
    visibility: hidden;
  }
  #section-to-print, #section-to-print * {
    visibility: visible;
  }
  #section-to-print {
    position: absolute;
    left: 0;
    top: 0;
  }
  #no-print {
    display: none;
  }
}

.custom-textarea {
  min-height: 110px;
}

.text-link {
  text-decoration: underline;
}

.table-body
{
  display: flex;
  align-items: center;
  cursor: grab;
  line-height: 1;
  margin-bottom: 0.1em;
  border-radius: 5px;
}

.table-body:active{
  cursor: grabbing;
}

.table-body:hover {
  background-color:rgb(221, 221, 221)!important;
}

.dropArea {
  position: relative;
}

.dropArea::before {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
}

.page-wrapper .page-body-wrapper .page-sidebar.open {
  margin-left: calc(-190px)!important;
}

.page-wrapper .page-body-wrapper .page-sidebar {
  width: 185px!important;
}

.page-wrapper .page-main-header.open {
  margin-left: 0!important;
  width: 100%!important;
}

.page-wrapper .page-main-header {
  width: calc(100% - 185px)!important;
  margin-left: 185px!important;
}

.page-wrapper .page-body-wrapper .page-sidebar ~ .page-body.open  {
  margin-left: 5px!important;
}

.page-wrapper .page-body-wrapper .page-sidebar ~ .page-body {
  margin-left: 185px!important;
}

.page-wrapper .page-body-wrapper footer.open {
  margin-left: 5px!important;
}

.page-wrapper .page-body-wrapper footer {
  margin-left: 185px!important;
}

.pointer {
  cursor: pointer!important;
}

.project-tree:nth-child(odd) {
   background: white;
 }

.project-tree:nth-child(even) {
  background: whitesmoke;
}

.required {
  color: #D83B48;
}

.required:after {
  content: " *";
}

.search-button {
  text-align: center;
  padding: 7px 0 7px;
  margin: 20px 25px 0;
}


